"use client";

import { useState } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Upload, CheckCircle2, XCircle } from "lucide-react";
import { observer } from "mobx-react-lite";

const ClaimCard = ({ shopId, shopName }) => {
  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);

  const handleFileSelect = () => {
    document.getElementById("hidden-file-input")?.click();
  };

  const handleFileChange = (e) => {
    const selected = e.target.files?.[0];
    if (!selected) return;

    setFile(selected);
    setPreview(URL.createObjectURL(selected));
    setResult(null);
  };

  return (
      <Card className="w-full shadow-md border rounded-xl overflow-hidden">

        {/* Header */}
        <div className="bg-gradient-to-r from-primary to-purple-500 text-white text-center py-4">
          <h1 className="text-lg font-semibold">Claim Your Stamp</h1>
          <p className="text-white/80 text-xs">Upload your UPI payment screenshot</p>
        </div>

        {/* Content */}
        <CardContent className="flex flex-col gap-5 py-5">

          {/* Shop Name */}
          <h2 className="text-center text-lg font-semibold">{shopName || "Shop"}</h2>

          {/* Upload Box */}
          <div
            onClick={handleFileSelect}
            className="
              flex flex-col items-center justify-center
              border-2 border-dashed border-gray-300 rounded-lg
              w-full cursor-pointer transition
              bg-gray-50 hover:bg-gray-100 p-4
            "
          >
            {!preview ? (
              <div className="flex flex-col items-center gap-2 text-gray-500">
                <Upload className="w-8 h-8 opacity-70" />
                <p className="text-sm font-medium">Tap to upload screenshot</p>
              </div>
            ) : (
              <div className="rounded-lg overflow-hidden border shadow-sm">
                <Image
                  src={preview}
                  alt="preview"
                  width={260}
                  height={260}
                  className="object-cover"
                />
              </div>
            )}
          </div>

          <input
            id="hidden-file-input"
            type="file"
            accept="image/*"
            onChange={handleFileChange}
            className="hidden"
          />

          {/* Submit Button */}
          <Button
            disabled={!file || loading}
            className="w-full py-3 mt-1 text-md font-medium rounded-lg"
          >
            {loading ? "Verifying..." : "Submit Screenshot"}
          </Button>

          {/* Result Messages */}
          {result?.success && (
            <Alert className="bg-green-50 border-green-300">
              <CheckCircle2 className="h-5 w-5 text-green-700" />
              <AlertTitle className="text-green-700 font-semibold">
                Payment Verified!
              </AlertTitle>
              <AlertDescription className="text-green-600">
                Stamp added successfully ðŸŽ‰
              </AlertDescription>
            </Alert>
          )}

          {result?.error && (
            <Alert variant="destructive">
              <XCircle className="h-5 w-5" />
              <AlertTitle className="font-semibold">Verification Failed</AlertTitle>
              <AlertDescription>{result.error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>
  );
};

export default observer(ClaimCard);
