"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Upload, CheckCircle2, XCircle } from "lucide-react";
import InvalidQRUI from "./invalidQRUI";
import ClaimCard from "./claimCard";
import { toast } from "sonner";


const ClaimPage = ({ shopId }) => {

    const [isInvalid, setIsInvalid] = useState(false); // <-- main fix

    /** 1ï¸âƒ£ If shopId is missing, immediately show Invalid QR */
    if (!shopId) {
        return <InvalidQRUI />;
    }

    /** 2ï¸âƒ£ Validate shopId from API */
    useEffect(() => {
        const checkShop = async () => {
            try {
                const res = await fetch("/api/public/checkShop", {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ shopId }),
                });

                const data = await res.json();

                if (!data.exists) {
                    setIsInvalid(true); // <-- correct way to trigger UI
                }
            } catch (error) {
                console.error(error);
                toast.error(error.message);
            }
        };

        if (shopId) {
            checkShop();
        }
    }, [shopId]);

    /** 3ï¸âƒ£ If shopId is invalid â†’ show the UI */
    if (isInvalid) {
        return <InvalidQRUI />;
    }


    return (
        <div className="relative flex justify-center items-center bg-custom-gradient h-screen p-5">
            <ClaimCard shopId={shopId} />

            {/* POWERED BY â€” BOTTOM CENTER */}
            <p className="absolute bottom-5 text-center text-xs text-gray-400 w-full">
                Powered by <span className="font-semibold">LoyaltyPro</span>
            </p>
        </div>
    );
};

export default ClaimPage;
